import React from 'react';
import s from './Nav.module.scss';
import {Link} from "react-scroll";

const Nav = () => {
    return (
        <div className={s.headersLinks}>
            <Link activeClass={s.active}  to='headphones' spy={true} smooth={true} offset={0} duration={500}></Link>
            <Link activeClass={s.active}  to='antenna' spy={true} smooth={true} offset={0} duration={500}></Link>
            <Link activeClass={s.active}  to='battery' spy={true} smooth={true} offset={0} duration={500}></Link>
            <Link activeClass={s.active}  to='battery' spy={true} smooth={true} offset={0} duration={500}> </Link>

        </div>
    );
};

export default Nav;